import React from 'react';
import Java from '../../assets/Java.webp';

const LearningDashboard = () => {
  return (
    <div className="mx-4 sm:mx-12 lg:mx-24">
      <div className="container mx-auto flex lg:flex-row flex-col items-center lg:space-x-20 space-y-10 lg:space-y-0 leading-tight py-20 text-gray-700 px-5">
        <div className="mb-10 lg:mb-0 cursor-pointer">
          <div className="relative max-w-full overflow-hidden">
            <div className="block max-w-full">
              <img
                alt=""
                aria-hidden="true"
                src="data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iNTQ0IiBoZWlnaHQ9IjMwOCIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIiB2ZXJzaW9uPSIxLjEiLz4="
                className="max-w-full block margin-0 border-none p-0"
              />
            </div>
            <img
              src={Java}
              decoding="async"
              data-nimg="intrinsic"
              className="hover:shadow-md absolute inset-0 box-border p-0 border-none m-auto block w-0 h-0 min-w-full max-w-full min-h-full max-h-full"
            />
          </div>
        </div>

        <div>
          <h2 className="text-2xl lg:text-4xl font-semibold mb-6">
            Comprehensive Learning Dashboard for All Ages
          </h2>
          <ul className="mx-0 pl-5 my-10 flex flex-col space-y-4 text-lg">
            <li>Manage and track class progress effortlessly</li>
            <li>Reschedule classes with ease</li>
            <li>Access session notes</li>
            <li>Receive certificates upon course completion</li>
            <li>Detailed course and instructor information</li>
            <li>Participate in global coding competitions, masterclasses, quizzes, and access various resources</li>
          </ul>
          <button className="p-4 inline-block align-middle border rounded focus:outline-none font-semibold px-16 h-13 text-lg bg-green-500 text-white hover:bg-orange-400 active:bg-orange-800 lg:w-auto w-full">
            Try a Free Class
          </button>
        </div>
      </div>
    </div>
  );
};

export default LearningDashboard;
