import React from 'react';
import Java from '../../assets/Java.webp';

const CoursesSection = () => {
  const courses = [
    {
      title: "Java Full Stack Development",
      lessons: "120 Lessons",
      activities: "300+ Activities",
      duration: "4-6 months",
      description: "Master full-stack development with Java through comprehensive lessons.",
      outcomes: [
        "Learn front-end and back-end technologies",
        "Build dynamic web applications",
        "Project-based learning",
        "Certificate accredited by industry standards"
      ],
      link: "https://qbeeacademy.in/software",
      curriculum: "path/to/java-curriculum.pdf",
      imgSrc: Java,
    },
    {
      title: "Python Full Stack Development",
      lessons: "130 Lessons",
      activities: "350+ Activities",
      duration: "4-6 months",
      description: "Become a full-stack developer using Python and build amazing applications.",
      outcomes: [
        "Hands-on experience with Python frameworks",
        "Create web applications and APIs",
        "Project-based learning",
        "Certificate accredited by industry standards"
      ],
      link: "https://qbeeacademy.in/software",
      curriculum: "path/to/java-curriculum.pdf",
      imgSrc: Java,
    },
    {
      title: "Core Java",
      lessons: "100 Lessons",
      activities: "250+ Activities",
      duration: "3-4 months",
      description: "Learn the fundamentals of Java programming with practical applications.",
      outcomes: [
        "Strong foundation in Java programming",
        "Understand OOP concepts",
        "Project-based learning",
        "Certificate accredited by industry standards"
      ],
      link: "https://qbeeacademy.in/software",
      curriculum: "path/to/java-curriculum.pdf",
      imgSrc: Java,
    },
    {
      title: "Core Python",
      lessons: "100 Lessons",
      activities: "250+ Activities",
      duration: "3-4 months",
      description: "Get started with Python programming and build your first applications.",
      outcomes: [
        "Understand Python syntax and semantics",
        "Learn basic programming concepts",
        "Project-based learning",
        "Certificate accredited by industry standards"
      ],
      link: "https://qbeeacademy.in/software",
      curriculum: "path/to/java-curriculum.pdf",
      imgSrc: Java,
    },
    {
      title: "React Development",
      lessons: "110 Lessons",
      activities: "300+ Activities",
      duration: "3-4 months",
      description: "Learn React and build interactive user interfaces for web applications.",
      outcomes: [
        "Master component-based architecture",
        "Build dynamic web applications",
        "Project-based learning",
        "Certificate accredited by industry standards"
      ],
      link: "https://qbeeacademy.in/software",
      curriculum: "path/to/java-curriculum.pdf",
      imgSrc: Java,
    },
    {
      title: "SQL",
      lessons: "90 Lessons",
      activities: "200+ Activities",
      duration: "2-3 months",
      description: "Learn SQL to manage and manipulate databases effectively.",
      outcomes: [
        "Understand relational database concepts",
        "Write complex SQL queries",
        "Project-based learning",
        "Certificate accredited by industry standards"
      ],
      link: "https://qbeeacademy.in/software",
      curriculum: "path/to/java-curriculum.pdf",
      imgSrc: Java,
    },
    {
      title: "Amazon Web Services (AWS)",
      lessons: "130 Lessons",
      activities: "350+ Activities",
      duration: "5-6 months",
      description: "Learn AWS and manage cloud services effectively.",
      outcomes: [
        "Understand cloud computing concepts",
        "Learn to deploy and manage applications on AWS",
        "Project-based learning",
        "Certificate accredited by industry standards"
      ],
      link: "https://qbeeacademy.in/software",
      curriculum: "path/to/aws-curriculum.pdf",
      imgSrc: Java,
    },
    {
      title: "Microsoft Azure",
      lessons: "120 Lessons",
      activities: "300+ Activities",
      duration: "5-6 months",
      description: "Master Microsoft Azure and build scalable cloud solutions.",
      outcomes: [
        "Learn Azure infrastructure and services",
        "Deploy and manage cloud applications",
        "Project-based learning",
        "Certificate accredited by industry standards"
      ],
      link: "https://qbeeacademy.in/software",
      curriculum: "path/to/azure-curriculum.pdf",
      imgSrc: Java,
    },
    {
      title: "DevOps",
      lessons: "110 Lessons",
      activities: "300+ Activities",
      duration: "5-6 months",
      description: "Learn DevOps practices and tools to streamline software development.",
      outcomes: [
        "Understand CI/CD pipelines",
        "Learn automation and monitoring tools",
        "Project-based learning",
        "Certificate accredited by industry standards"
      ],
      link: "https://qbeeacademy.in/software",
      curriculum: "path/to/devops-curriculum.pdf",
      imgSrc: Java,
    },
    {
      title: "Materials Management",
      lessons: "90 Lessons",
      activities: "200+ Activities",
      duration: "5-6 months",
      description: "Learn to manage materials effectively in a supply chain environment.",
      outcomes: [
        "Understand inventory management",
        "Learn procurement processes",
        "Project-based learning",
        "Certificate accredited by industry standards"
      ],
      link: "https://qbeeacademy.in/software",
      curriculum: "path/to/materials-curriculum.pdf",
      imgSrc: Java,
    },
    {
      title: "Finance & Accounting",
      lessons: "100 Lessons",
      activities: "250+ Activities",
      duration: "5-6 months",
      description: "Gain expertise in finance and accounting principles and practices.",
      outcomes: [
        "Understand financial statements",
        "Learn accounting techniques",
        "Project-based learning",
        "Certificate accredited by industry standards"
      ],
      link: "https://qbeeacademy.in/software",
      curriculum: "path/to/finance-curriculum.pdf",
      imgSrc: Java,
    },
    {
      title: "Sales & Distribution",
      lessons: "120 Lessons",
      activities: "300+ Activities",
      duration: "5-6 months",
      description: "Master the skills needed for effective sales and distribution management.",
      outcomes: [
        "Learn sales strategies",
        "Understand distribution channels",
        "Project-based learning",
        "Certificate accredited by industry standards"
      ],
      link: "https://qbeeacademy.in/software",
      curriculum: "path/to/sales-curriculum.pdf",
      imgSrc: Java,
    },
  ];
  
  const handleDownload = (curriculum) => {
    const link = document.createElement('a');
    link.href = curriculum;
    link.download = curriculum.split('/').pop();
    link.click();
  };

  return (
    <div className="mx-0 sm:ml-12 sm:mr-12">
      <div className="container mx-auto flex flex-col space-y-10 leading-tight py-16 text-gray-700 px-5 sm:px-10" id="courses">
        <div className="text-center">
          <h2 className="text-2xl lg:text-4xl font-semibold mb-4">
            Choose a <span className="underlined-text">coding course</span> That Elevates Your Career
          </h2>
        </div>

        <div className="flex flex-wrap justify-center">
          {courses.map((course, index) => (
            <a
              key={index}
              href={course.link}
              className="p-4 w-full sm:w-1/2 md:w-1/3 lg:w-1/4"
            >
              <div className="pb-0 shadow hover:shadow-lg transition-all duration-200 rounded-md bg-white hover:bg-violet-50 hover:border-violet-100 border border-gray-100 flex flex-col h-full">
                <div className="relative w-full">
                  <div className="absolute top-0 left-0 z-10 bg-green-500 rounded-md shadow-md px-3 py-1 text-white font-semibold">
                    {index < 6 ? 'Programming' : index < 9 ? 'Cloud' : 'SAP'}
                  </div>
                  <div className="relative overflow-hidden">
                    <img
                      alt={course.title}
                      srcSet={`${course.imgSrc}?auto=webp&optimize=medium&width=640 1x, ${course.imgSrc}?auto=webp&optimize=medium&width=1080 2x`}
                      src={course.imgSrc}
                      className="max-w-full"
                      loading="lazy"
                    />
                  </div>
                </div>
                <div className="space-y-2 pt-3 pb-2 p-3 flex-grow">
                  <a href={course.link} target="_blank" rel="noopener noreferrer">
                    <h3 className="leading-tight font-semibold duration-300 cursor-pointer hover:text-green-600" style={{ fontSize: '1.4rem' }}>
                      {course.title}
                    </h3>
                  </a>
                  <div className="flex text-sm flex-wrap">
                    <div className="flex items-center mr-2">
                      <span className="text-green-500">{course.activities}</span>
                    </div>
                    <div className="flex items-center mr-2">
                      <span className="text-green-500">{course.lessons}</span>
                    </div>
                    <div className="flex items-center">
                      <span className="text-green-500">{course.duration}</span>
                    </div>
                  </div>
                  <p className="text-sm" style={{ minHeight: '70px' }}>
                    {course.description}
                  </p>
                  <h4 className="font-semibold">Learning outcomes</h4>
                  <div className="space-y-1 text-sm">
                    {course.outcomes.map((outcome, i) => (
                      <div key={i} className="flex items-start">
                        <span className="rounded-full bg-green-200" style={{ padding: '2px', marginTop: '2px' }}>
                          <svg xmlns="http://www.w3.org/2000/svg" width="9" height="9" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="3" strokeLinecap="round" strokeLinejoin="round" className="text-orange-500">
                            <polyline points="20 6 9 17 4 12" />
                          </svg>
                        </span>
                        <p className="ml-1 text-left">{outcome}</p>
                      </div>
                    ))}
                  </div>
                </div>
                <div className="px-3 flex justify-center">
                  <a
                    className="btn border border-green-500 text-green-500 hover:bg-orange-500 hover:text-white duration-300 w-full text-center text-lg"
                    href={course.link}
                  >
                    Try a free class
                  </a>
                </div>

                <button
                  className="text-sm flex items-center align-middle font-semibold py-2 h-10 text-green-500 mx-auto focus:outline-none z-20 hover:underline relative"
                  onClick={() => handleDownload(course.curriculum)}
                >
                  <span className="px-1">Download curriculum</span>
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="15"
                    height="15"
                    viewBox="0 0 24 24"
                    fill="none"
                    stroke="currentColor"
                    strokeWidth="2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  >
                    <path d="M21 15v4a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2v-4" />
                    <polyline points="7 10 12 15 17 10" />
                    <line x1="12" y1="15" x2="12" y2="3" />
                  </svg>
                </button>
              </div>
            </a>
          ))}
        </div>
      </div>
    </div>
  );
};

export default CoursesSection;
