import React, { useState } from 'react';
import { FaFlag } from 'react-icons/fa';
import Img from '../../assets/Main_2.webp';

const HeroSection = () => {
  const [phoneNumber, setPhoneNumber] = useState('+91');
  const [isVisible, setIsVisible] = useState(false);

  const handleInputChange = (e) => {
    setPhoneNumber(e.target.value);
  };

  const handleButtonClick = async () => {
    const formData = new FormData();
    formData.append('phoneNumber', phoneNumber);

    try {
      const response = await fetch('https://formspree.io/f/xvgpgbwl', {
        method: 'POST',
        headers: {
          Accept: 'application/json',
        },
        body: formData,
      });

      if (response.ok) {
        alert('Phone number submitted successfully!');
        setPhoneNumber('+91'); // Reset the phone number field
      } else {
        alert('Failed to submit phone number');
      }
    } catch (error) {
      alert('An error occurred while submitting the phone number');
    }
  };

  return (
    <div className="relative flex justify-center px-5 md:px-3 pt-8 pb-10 overflow-visible lg:pb-20 lg:rounded-b-lg bg-gradient-to-r from-green-500 to-blue-500 mt-8">
      <div className="relative container flex flex-col mx-auto text-white z-10 lg:flex-row lg:items-center lg:justify-between">
        <div className="z-10 flex flex-col items-center space-y-5 text-center lg:items-start lg:text-left lg:mt-10 lg:mx-10">
          <div className="flex space-x-4 items-center text-white">
            <div>
              <p>Join our institute</p>
            </div>
            <span className="h-10 bg-gray-700" style={{ width: '1px' }}></span>
            <div>
              <p>Start your coding journey today</p>
            </div>
          </div>
          <h1 className="max-w-2xl text-2xl leading-tight font-bold lg:text-4xl lg:leading-relaxed">
            Software training for college students to become the{' '}
            <span className="">innovators</span> of the{' '}
            <span className="">future</span>
          </h1>
          <div className="text-left w-full">
            <div className="flex w-full flex-col md:flex-row md:space-x-2">
              <div className="relative w-full md:w-2/4 mt-4 md:mt-0">
                <FaFlag className="absolute left-3 top-1/2 transform -translate-y-1/2 w-6 h-6 text-gray-700" />
                <input
                  className="appearance-none w-full py-4 px-12 rounded-sm text-gray-800 leading-tight focus:outline-none"
                  placeholder="Phone number"
                  type="tel"
                  id="phone"
                  name="phone"
                  required
                  value={phoneNumber}
                  onChange={handleInputChange}
                />
              </div>
              <div className="mt-2 md:mt-0 w-full md:w-2/4">
                <button
                  className="w-full text-lg bg-blue-500 hover:bg-blue-600 text-white font-bold lg:text-xl lg:px-3 xl:px-6 p-3 transition duration-300 ease-in-out"
                  onClick={handleButtonClick}
                >
                  Try a free class
                </button>
              </div>
            </div>
            <div className="mt-3">
              <div className="flex justify-center lg:justify-start flex-wrap space-y-2 md:space-x-6">
                <div className="flex items-center space-x-1">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="18"
                    height="18"
                    viewBox="0 0 24 24"
                    fill="none"
                    stroke="currentColor"
                    strokeWidth="2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    className="text-orange-500"
                  >
                    <rect x="2" y="3" width="20" height="14" rx="2" ry="2"></rect>
                    <line x1="8" y1="21" x2="16" y2="21"></line>
                    <line x1="12" y1="17" x2="12" y2="21"></line>
                  </svg>
                  <p>
                    Free class <span className="line-through">worth ‚Çπ1500</span>
                  </p>
                </div>
                <div className="flex items-center space-x-1">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="18"
                    height="18"
                    viewBox="0 0 24 24"
                    fill="none"
                    stroke="currentColor"
                    strokeWidth="2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    className="text-orange-500"
                  >
                    <polygon points="13 2 3 14 12 14 11 22 21 10 12 10 13 2"></polygon>
                  </svg>
                  <p>100+ classes booked in the last 24 hours</p>
                </div>
              </div>
            </div>
          </div>
          <div className="flex justify-center space-x-10 lg:justify-start text-white">
            <div className="flex flex-col items-center lg:items-start">
              <span className="inline-block text-xl font-bold">50+</span>
              <p className="relative inline-block text-center lg:text-left">
                Students
                <br />
                <span className="absolute bottom-0 inline-block w-6 border-b border-gray-600"></span>
              </p>
            </div>
            <div className="flex flex-col items-center lg:items-start">
              <span className="inline-block text-xl font-bold">15+</span>
              <p className="relative inline-block text-center lg:text-left">
                Courses
                <br />
                <span className="absolute bottom-0 inline-block w-6 border-b border-gray-600"></span>
              </p>
            </div>
          </div>
        </div>
        <div className="relative flex flex-col items-center justify-center mx-auto mt-8 lg:mt-0 lg:ml-10">
          <img
            src={Img}
            alt="QBee hero Img"
            className={`w-full h-auto max-w-lg max-h-lg lg:max-w-xl lg:max-h-xl ${
              isVisible ? 'animate__animated animate__zoomIn' : ''
            } transition duration-500 ease-in-out`}
            onLoad={() => setIsVisible(true)} // Trigger animation on image load
          />
        </div>
      </div>
    </div>
  );
};

export default HeroSection;
