import React from 'react';
import Post from '../../assets/bot.webp'
const BottomHeroSection = () => {
  return (
    <div className="-mb-10">
      <div className="relative flex flex-col max-w-full px-10 mx-3 -mb-10 bg-white rounded-md lg:px-20 lg:flex-row lg:mx-auto lg:max-w-5xl h-full lg:h-48">
        <div className="flex flex-col justify-center w-full my-6 space-y-4 text-center text-block lg:text-left">
          <div className="text-3xl leading-tight font-bold">
            Discover the power of <br /> technology with us
          </div>
          <button className="px-10 py-3 text-lg bg-green-500 border-none lg:mr-auto text-white shadow-md">
            Explore our courses
          </button>
        </div>
        <div className="lg:-mt-16">
          <div className="inline-block max-w-full overflow-hidden relative">
            <div className="block max-w-full">
              <img
                alt=""
                aria-hidden="true"
                src="https://via.placeholder.com/400x400.png?text="
                className="max-w-full block m-0 border-none p-0"
              />
            </div>
            <img
              alt="coding course for kids"
              src={Post}
              decoding="async"
              className="absolute inset-0 box-border p-0 border-none m-auto block w-0 h-0 min-w-full max-w-full min-h-full max-h-full"
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default BottomHeroSection;
