import React, { useState } from 'react';

const FAQ = () => {
  const [openIndex, setOpenIndex] = useState(0); // Open the first FAQ by default

  const faqs = [
    {
      question: "What programming courses do you offer?",
      answer: "We offer a variety of programming courses including Java Full Stack Development, Python Full Stack Development, Core Java, Core Python, and React Development.",
    },
    {
      question: "Do you offer cloud training?",
      answer: "Yes, we offer comprehensive training in cloud technologies including Amazon Web Services (AWS) and Microsoft Azure.",
    },
    {
      question: "What are the prerequisites for the full stack development courses?",
      answer: "Basic knowledge of programming is recommended but not mandatory. Our courses are designed to cater to both beginners and experienced professionals.",
    },
    {
      question: "Do you provide DevOps training?",
      answer: "Yes, we offer specialized training in DevOps to help you understand the integration of development and operations for faster and more reliable software delivery.",
    },
    {
      question: "What SAP courses are available?",
      answer: "We provide training in SAP S4/HANA with a focus on Materials Management, Finance & Accounting, and Sales & Distribution.",
    },
    {
      question: "Are there any certification exams after completing the courses?",
      answer: "Yes, we offer certification exams for various courses to validate your skills and knowledge in the respective domains.",
    },
    {
      question: "How do I stay updated on my course progress?",
      answer: "We provide regular progress reports and updates. Additionally, Parent Teacher Meetings (PTMs) are conducted for students enrolled in courses designed for younger learners.",
    },
    {
      question: "Can I switch courses if I find the current one too easy or too difficult?",
      answer: "Absolutely! We want to ensure that each student is in the class that best fits their learning pace and skill level.",
    },
    {
      question: "Is there a refund policy for the courses?",
      answer: "Yes, we offer a refund policy within a certain timeframe after the purchase. Please refer to our terms and conditions for details.",
    },
    {
      question: "What platforms do you use for online classes?",
      answer: "We use reliable online platforms that are easy to navigate, ensuring a smooth learning experience for students.",
    },
  ];

  const toggleFAQ = (index) => {
    setOpenIndex(openIndex === index ? null : index);
  };

  return (
    <div className="container mx-auto flex flex-col lg:flex-row gap-x-20 max-w-4xl text-grey py-16">
      <h2 className="font-600 text-3xl leading-tight lg:text-5xl mt-3 text-center lg:text-left">FAQ</h2>
      <div className="flex flex-col px-5 lg:px-0">
        {faqs.map((faq, index) => (
          <div key={index}>
            <button
              className="flex justify-between py-3 my-3 group w-full focus:outline-none"
              onClick={() => toggleFAQ(index)}
            >
              <span className="text-lg text-grey font-600 group-hover:text-orange text-left w-4/5">
                {faq.question}
              </span>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="24"
                height="24"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
                className="text-grey-800 group-hover:text-orange w-4"
              >
                <line x1="12" y1="5" x2="12" y2="19" />
                <line x1="5" y1="12" x2="19" y2="12" />
              </svg>
            </button>
            <div className={`text-sm text-grey overflow-hidden duration-200 border-b border-grey-300 ${openIndex === index ? "max-h-full" : "max-h-0"}`}>
              <div>
                <p className="text-base">{faq.answer}</p>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default FAQ;
